<template >
    <section class="overlay">
        <div class="modal">
            <button class="btn-close" @click="close()"><svg xmlns="http://www.w3.org/2000/svg" height="24" width="24" ><path d="M6.4 19 5 17.6l5.6-5.6L5 6.4 6.4 5l5.6 5.6L17.6 5 19 6.4 13.4 12l5.6 5.6-1.4 1.4-5.6-5.6Z"/></svg></button>
            <form  class="modal-form">
                <label>Plant Title:</label>
                <input type="text" placeholder="Plant Title" v-model="newPlant.title" required />
                <label>Plant Price:</label>
                <input type="number" min="1" step="any" placeholder="Plant Price" v-model="newPlant.price" required />
                <label>Plant Description:</label>
                <textarea cols="30" rows="6" v-model="newPlant.description" maxlength="300" required></textarea>
                <label>Plant Image URL:</label>
                <input type="text" placeholder="Plant Image Url" v-model="newPlant.img" required>
            </form>
            <div class="img-save">
                <img v-if="newPlant.img" :src="newPlant.img" alt="">
                <div v-else class="no-img">
                    <p>PLANT IMAGE</p>
                </div>
                <button @click.prevent="save">Save</button>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    props: {
        plant: {
            type: Object,
            required: true,
        },

    },
    data() {
        return {
            newPlant: JSON.parse(JSON.stringify(this.plant))
        }
    },
    methods: {
        close() {
            this.$emit('close')
        },
        save() {
            if(!this.newPlant.title || !this.newPlant.img|| !this.newPlant.price|| !this.newPlant.description) return
            this.$emit('savePlant', this.newPlant)
            this.close()
        }
    }
}
</script>