<template>
  <section class="admin">
    <div class="add-plant">
      <h1>Welcome Admin</h1>
      <button @click="openForm(plantToEdit)">+ Add Plant</button>
    </div>
    <div class="plants-table">
      <table>
        <tr>
          <!-- <th>ID</th> -->
          <th>Title</th>
          <th>Price</th>
          <th>Option</th>
        </tr>
        <tr v-for="plant in plants" :key="plant._id">
          <!-- <td>{{ plant._id }}</td> -->
          <td>{{ plant.title }}</td>
          <td>{{ plant.price }} $</td>
          <td class="btns">
            <button class="edit" @click="openForm(plant)">Edit</button>
            <button class="delete" @click="remove(plant)">Delete</button>
          </td>
        </tr>
      </table>
      <save-modal v-if="isOpen" :plant="plant" @close="closeForm" @savePlant="save"></save-modal>
    </div>
  </section>
</template>

<script>
import { plantService } from '../services/plant-service.js'
import saveModal from '../components/save-modal.vue'

export default {
  name: 'AdminView',
  data() {
    return {
      plant: null,
      plantToEdit: plantService.getEmptyPlant(),
      isOpen: false,
    }
  },
  methods: {
    remove(plant) {
      this.$store.dispatch({ type: 'removePlant', id: plant._id })
    },
    openForm(plant) {
      this.plant = plant
      this.isOpen = true
    },
    closeForm() {
      this.isOpen = false
    },
    save(newPlant) {
      this.$store.dispatch({ type: 'savePlant', plant: newPlant })
    },
  },
  computed: {
    plants() {
      return this.$store.getters.plants
    },
  },
  components: {
    saveModal,
  }
}
</script>